import InfoTable from "../../plugin/components/InfoTable";
import PageInfo from "../../plugin/script/common/PageInfo";
import BaseIframe from "../../plugin/script/common/BaseIframe";
import PermissionButton from "../../plugin/components/PermissionButton";
import InputArea from "../../plugin/components/InputArea";
import SelectTime from "../../plugin/components/SelectTime";
import InfoTableItem from "../../plugin/components/InfoTableItem";
import { truncate } from "fs";

export default {
  name: "Universal",
  extends: BaseIframe,
  components: {
    InfoTableItem,
    InputArea,
    SelectTime,
    InfoTable,
    PermissionButton
  },
  data() {
    return {
      format: {
        platform: ""
      },
      platforms: [
        { label: "平台一", value: "1" },
        { label: "平台二", value: "2" }
      ],
      pickerOptions: {
        disabledDate(time) {
          return time.getTime() > Date.now();
        },
        shortcuts: [
          {
            text: "今天",
            onClick(picker) {
              picker.$emit("pick", new Date());
            }
          },
          {
            text: "昨天",
            onClick(picker) {
              const date = new Date();
              date.setTime(date.getTime() - 3600 * 1000 * 24);
              picker.$emit("pick", date);
            }
          },
          {
            text: "一周前",
            onClick(picker) {
              const date = new Date();
              date.setTime(date.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit("pick", date);
            }
          }
        ]
      },
      date: [],
      tableStyle: [
        { label: "日期", prop: "time", width: "" },
        { label: "用户ID", prop: "user_id", width: "" },
        { label: "所属渠道", prop: "user_desc", width: "" },
        { label: "直属玩家税收", prop: "tax", width: "" },
        { label: "直属玩家返利", prop: "getCom", width: "" },
        { label: "其他玩家税收", prop: "getCom", width: "" },
        { label: "其他玩家返利", prop: "getCom", width: "" },
        { label: "总返利金额", prop: "getCom", width: "" }
      ],
      records: [
        {
          time: "2019-09-01 12:00:00",
          user_id: "100012",
          user_desc: "这边是昵称",
          tax: "100.00",
          getCom: "50.00"
        }
      ],
      pageInfo: new PageInfo(0, [5, 10, 15], 0),
      dialogVisible: false,
      dialogModelVisible: false,
      formData: {
        mode: "税收",
        ratio: ""
      },
      form: {
        ratio: "30%"
      }
    };
  },
  methods: {
    search() {},
    /** 表格操作 **/
    handelClick(btn) {}
  }
};



